}
footer.page-footer {
  margin: 0;
}
#index-banner input:not([type]),
#index-banner input[type=text],
#index-banner input[type=password],
#index-banner input[type=email],
#index-banner input[type=url],
#index-banner input[type=time],
#index-banner input[type=date],
#index-banner input[type=datetime],
#index-banner input[type=datetime-local],
#index-banner input[type=tel],
#index-banner input[type=number],
#index-banner input[type=search],
#index-banner textarea.materialize-textarea,
#index-banner .select-wrapper input.select-dropdown {
  padding-left: 10px;
  border: 1px solid #9e9e9e;
}
.input-field .checkin-label {
  font-size: 1.2rem;
  color: white;
}
.button-home {
  z-index: 0;
}
.picker {
  z-index: 1;
}
.my-slider {
  text-align: left;
}
.unslider-nav {
  background-color: transparent;
}
#home-email-button {
  margin-bottom: 15px;
}
.footer-contact img {
  height: 50px;
}
.none {
  display: none;
}
.margin-top-21 {
  margin-top: 21px;
}
#logo-container > img {
  height: 65px;
}
.slider .indicators .indicator-item.active {
  background-color: #F7E7CE;
}
.about-gallery-title {
  margin-top: 20px;
  margin-right: 15px;
  margin-bottom: 5px;
}
.map-frame {
  width: 100%;
  height: 100%;
  position: relative;
}
.row .pad-left-0 {
  padding-left: 0px;
}
.margin-top-5 {
  margin-top: 5px;
}
.contact-box {
  margin-top: 118px;
}
.contact-box label {
  top: -1.2rem;
}
.contact-box .range-field {
  margin-top: 113px;
}
.fire-3,
.fire-2,
.fire-1 {
  visibility: hidden;
}
.margin-top-20 {
  margin-top: 20px;
}
@media only screen and (max-width: 992px) {
  .parallax-container .section {
    top: 40%;
  }
  #index-banner .section {
    top: 10%;
  }
  .contact-box {
    margin-top: 121px;
  }
  .contact-box label {
    top: -2.1rem;
  }
  .contact-box .materialize-textarea {
    padding: 1.1rem 0;
  }
}
@media only screen and (max-width: 600px) {
  #index-banner .section {
    top: 0;
  }
  .home-email-wrapper {
    min-height: 0px;
  }
  #block {
    display: block !important;
  }
  #logo-container > img {
    height: 56px;
  }
  .a100 {
    width: 100%;
  }
  .margin-top-10-s {
    margin-top: 10px;
  }
  .contact-box {
    margin-top: 3rem;
  }
  .contact-submit {
    width: 100%;
  }
}
.image-source-link {
  color: #98C3D1;
}
.mfp-with-zoom .mfp-container,
.mfp-with-zoom.mfp-bg {
  opacity: 0;
  -webkit-backface-visibility: hidden;
  /* ideally, transition speed should match zoom duration */
  -webkit-transition: all 0.3s ease-out;
  -moz-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}
.mfp-with-zoom.mfp-ready .mfp-container {
  opacity: 1;
}
.mfp-with-zoom.mfp-ready.mfp-bg {
  opacity: 0.8;
}
.mfp-with-zoom.mfp-removing .mfp-container,
.mfp-with-zoom.mfp-removing.mfp-bg {
  opacity: 0;
}
